import React from 'react'
import {Card,Button,Input, Table,Pagination, Form,Modal} from 'antd'
import UserInfo from './UserInfo'
import './index.css'
const columns = (showDetail) => [
{
  title: '',
  dataIndex: 'name',
}, {
  title: '',
  dataIndex: 'alias',
}, {
  title: '',
  dataIndex: 'descript',
}, {
  title: '',
  dataIndex: 'user_group_name',
}, {
  title: '',
  dataIndex: '',
  render:(text,record)=>(<Button className="noBorBtn" type="dashed" onClick={() => showDetail(record)}></Button>)
}];

const data = [{
  key: 1,
  name:'',
  alias:'',
  descript:'',
  user_group_name:'',
},{
  key: 2,
  name:'',
  alias:'',
  descript:'',
  user_group_name:'',
},{
  key: 3,
  name:'',
  alias:'',
  descript:'',
  user_group_name:'',
},{
  key: 4,
  name:'',
  alias:'',
  descript:'',
  user_group_name:'',
},{
  key: 5,
  name:'',
  alias:'',
  descript:'',
  user_group_name:'',
},{
  key: 6,
  name:'',
  alias:'',
  descript:'',
  user_group_name:'',
},{
  key: 7,
  name:'',
  alias:'',
  descript:'',
  user_group_name:'',
},{
  key: 8,
  name:'',
  alias:'',
  descript:'',
  user_group_name:'',
},{
  key: 9,
  name:'',
  alias:'',
  descript:'',
  user_group_name:'',
},{
  key: 10,
  name:'',
  alias:'',
  descript:'',
  user_group_name:'',
}];
@Form.create()
export default class CPCompanyManage extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      tableLoading: false,
      selectedRowKeys: [],
      pageNo: 1,
      gameFormData:{},
      visible: false,
      userinfo:{}
    }
    // this.addData = this.addData.bind(this)
    // this.editData = this.editData.bind(this)
    // this.deletData = this.deletData.bind(this)
    this.onShowSizeChange = this.onShowSizeChange.bind(this)
    this.changePageNo = this.changePageNo.bind(this)
    this.showDetail = this.showDetail.bind(this)
  }
  componentDidMount() {
    this.props.form.validateFields();
  }
  showDetail=(val)=>{ //
    this.setState({
      visible: true,
      userinfo: val
    });
    console.log(this.state.userinfo)
  }

  changePageNo(page, pagesize) {// pageno
    console.log(page)
    console.log(pagesize)
  }

  onShowSizeChange(current, pageSize) { // pagesize
    console.log(current, pageSize);
  }

  handleSubmit = (e) => { // 
    e.preventDefault();
    console.log(e)
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  }

  render(){
    const {
      getFieldDecorator
    } = this.props.form;
    return(
      <div className="content fadein">
        <div>
          <div className="inputRight">
            <Form layout="inline" onSubmit={this.handleSubmit}>
              <Form.Item>
                {getFieldDecorator('company', {})(
                  <Input placeholder="" />
                )}
              </Form.Item>
              <Form.Item>
                <Button
                  type="primary"
                  htmlType="submit"
                >
                  
                </Button>
              </Form.Item>
            </Form>
          </div>
            {/* <Button type="primary" style={{marginBottom:"20px"}} onClick={this.addData()} shape="round" icon="plus"></Button> */}
            <Table loading={this.state.tableLoading} pagination={false} className="nopadtable" columns={columns(this.showDetail)} dataSource={data} />,
            <div className="mb20" style={{ textAlign: "right" }}>
              <Pagination showSizeChanger onChange={this.changePageNo} onShowSizeChange={this.onShowSizeChange} defaultCurrent={this.state.pageNo} total={500} />
            </div>
          </div>

          <UserInfo visible={this.state.visible} userinfo={this.state.userinfo}/>
      </div>
    )
  }
}




