import React from 'react'
import {Modal} from 'antd'
export default class userInfo extends React.Component{
  componentDidUpdate(){
    console.log(this.props.userinfo)
  }
  render(){
    const {visible,userinfo} = this.props
    return(
      <div>
        <Modal
          title="Basic Modal"
          visible={visible}
          maskClosable='false'
          footer={null}
        >
          <p>{userinfo.name}</p>
        </Modal> 
      </div>
    )
  }
}



